export default "import UICard from \"lib/src/components/UICard\";\nimport UIList from \"lib/src/components/UIList\";\n\nconst list1 = [\n    {\n        icon: 'camera',\n        label: 'Item 1',\n        after: 'CEO'\n    },\n    {\n        image: 'https://cdn.quasar.dev/img/boy-avatar.png',\n        label: 'Item 2',\n        after: After()\n    },\n    {\n        icon: 'camera',\n        label: 'Item 3'\n    }\n]\n\nconst list2 = [\n    {\n        icon: 'camera',\n        label: 'Item 1',\n        after: 'CEO',\n        link: 1\n    },\n    {\n        image: 'https://cdn.quasar.dev/img/boy-avatar.png',\n        label: 'Link without arrow',\n        after: After(),\n        link: 2\n    },\n    {\n        icon: 'camera',\n        label: 'Item 3',\n        link: true\n    }\n]\n\nfunction After() {\n    return <span className=\"badge\">5</span>\n}\n\nexport default function WithIcon() {\n    return (\n        <div>\n            <UICard>\n                <UIList list={list1} />\n            </UICard>\n\n            <UICard>\n                <UIList list={list2}/>\n            </UICard>\n        </div>\n    )\n}";