export default "import UICard from \"lib/src/components/UICard\";\nimport UIList from \"lib/src/components/UIList\";\n\nconst list = [\n    {\n        label: 'Item 1'\n    },\n    {\n        label: 'Item 2'\n    },\n    {\n        label: 'Item 3'\n    }\n]\n\nexport default function Basic() {\n    return (\n        <div>\n            <UICard>\n                <UIList list={list} />\n            </UICard>\n\n            <UICard>\n                <UIList list={list} link/>\n            </UICard>\n        </div>\n    )\n}";